.voice-recognition-container {
    background: white;
    border-radius: 15px;
    padding: 25px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    border: 1px solid #e1e5e9;
    margin-bottom: 20px;
}

.voice-controls {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.voice-button {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 15px 25px;
    border: none;
    border-radius: 50px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    min-width: 200px;
    justify-content: center;
}

.voice-button:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

.voice-button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
}

.voice-button.listening {
    background: linear-gradient(135deg, #ff6b6b 0%, #ee5a24 100%);
    animation: pulse-glow 2s infinite;
}

.voice-icon {
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.voice-status {
    font-weight: 500;
}

.pulse-animation {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    border-radius: 50px;
    background: rgba(255, 255, 255, 0.2);
    animation: pulse 1.5s infinite;
    pointer-events: none;
}

@keyframes pulse {
    0% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 1;
    }
    100% {
        transform: translate(-50%, -50%) scale(1.5);
        opacity: 0;
    }
}

@keyframes pulse-glow {
    0%, 100% {
        box-shadow: 0 10px 25px rgba(255, 107, 107, 0.3);
    }
    50% {
        box-shadow: 0 10px 25px rgba(255, 107, 107, 0.6);
    }
}

.voice-actions {
    display: flex;
    gap: 10px;
}

.action-btn {
    padding: 8px 15px;
    border: none;
    border-radius: 25px;
    font-size: 0.9rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 5px;
}

.clear-btn {
    background: #ffebee;
    color: #d32f2f;
}

.clear-btn:hover {
    background: #ffcdd2;
    transform: translateY(-1px);
}

.copy-btn {
    background: #e3f2fd;
    color: #1976d2;
}

.copy-btn:hover {
    background: #bbdefb;
    transform: translateY(-1px);
}

.transcript-container {
    min-height: 120px;
    border: 2px dashed #e1e5e9;
    border-radius: 10px;
    padding: 20px;
    background: #f8f9fa;
    transition: all 0.3s ease;
}

.transcript-container.has-content {
    border-color: #667eea;
    background: white;
}

.transcript-content {
    height: 100%;
}

.transcript-text {
    font-size: 1rem;
    line-height: 1.6;
    color: #333;
    margin-bottom: 15px;
    white-space: pre-wrap;
    word-wrap: break-word;
}

.interim-text {
    color: #666;
    font-style: italic;
    opacity: 0.7;
}

.transcript-stats {
    display: flex;
    gap: 20px;
    font-size: 0.85rem;
    color: #666;
    border-top: 1px solid #e1e5e9;
    padding-top: 10px;
}

.word-count, .char-count {
    display: flex;
    align-items: center;
    gap: 5px;
}

.transcript-placeholder {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    text-align: center;
    color: #666;
}

.placeholder-icon {
    font-size: 3rem;
    margin-bottom: 15px;
    opacity: 0.5;
}

.transcript-placeholder p {
    margin: 5px 0;
}

.placeholder-hint {
    font-size: 0.9rem;
    opacity: 0.7;
}

.voice-error {
    background: #fff3cd;
    border: 1px solid #ffeaa7;
    border-radius: 10px;
    padding: 15px;
    margin-top: 15px;
    display: flex;
    align-items: center;
    gap: 10px;
    color: #856404;
}

.error-icon {
    font-size: 1.5rem;
    flex-shrink: 0;
}

.voice-error p {
    margin: 0;
    font-size: 0.9rem;
}

.error-suggestion {
    font-size: 0.8rem;
    opacity: 0.8;
    margin-top: 5px !important;
}

.listening-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    margin-top: 15px;
    padding: 15px;
    background: rgba(102, 126, 234, 0.1);
    border-radius: 10px;
    color: #667eea;
    font-weight: 500;
}

.listening-dots {
    display: flex;
    gap: 5px;
}

.listening-dots span {
    width: 8px;
    height: 8px;
    background: #667eea;
    border-radius: 50%;
    animation: listening-bounce 1.4s infinite ease-in-out;
}

.listening-dots span:nth-child(1) {
    animation-delay: -0.32s;
}

.listening-dots span:nth-child(2) {
    animation-delay: -0.16s;
}

@keyframes listening-bounce {
    0%, 80%, 100% {
        transform: scale(0.8);
        opacity: 0.5;
    }
    40% {
        transform: scale(1);
        opacity: 1;
    }
}

/* Mobile responsiveness */
@media (max-width: 768px) {
    .voice-recognition-container {
        padding: 20px;
    }
    
    .voice-controls {
        flex-direction: column;
        align-items: stretch;
    }
    
    .voice-button {
        min-width: auto;
        width: 100%;
    }
    
    .voice-actions {
        justify-content: center;
    }
    
    .transcript-stats {
        flex-direction: column;
        gap: 10px;
    }
    
    .listening-indicator {
        flex-direction: column;
        gap: 10px;
    }
}

/* Accessibility improvements */
.voice-button:focus,
.action-btn:focus {
    outline: 2px solid #667eea;
    outline-offset: 2px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
    .voice-button {
        border: 2px solid #333;
    }
    
    .transcript-container {
        border-width: 3px;
    }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
    .voice-button,
    .action-btn,
    .pulse-animation,
    .listening-dots span {
        animation: none;
        transition: none;
    }
}

.retry-btn {
    margin-top: 10px;
    padding: 8px 16px;
    background: #667eea;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 0.9rem;
    font-weight: 500;
    transition: all 0.3s ease;
}

.retry-btn:hover {
    background: #5a6fd8;
    transform: translateY(-1px);
}

.initializing-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: 15px;
    padding: 15px;
    background: rgba(102, 126, 234, 0.1);
    border-radius: 10px;
    color: #667eea;
    font-weight: 500;
}

.loading-spinner {
    width: 20px;
    height: 20px;
    border: 2px solid rgba(102, 126, 234, 0.3);
    border-top: 2px solid #667eea;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
} 