.review-history {
    min-height: 100vh;
    background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
    padding: 20px;
}

.history-container {
    max-width: 1200px;
    margin: 0 auto;
}

.history-header {
    text-align: center;
    margin-bottom: 40px;
    color: #333;
}

.history-header h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-weight: 700;
}

.history-header p {
    font-size: 1.1rem;
    color: #666;
    margin: 0;
}

.filters-section {
    background: white;
    border-radius: 15px;
    padding: 25px;
    margin-bottom: 30px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.search-box {
    position: relative;
    margin-bottom: 20px;
}

.search-box input {
    width: 100%;
    padding: 12px 45px 12px 15px;
    border: 2px solid #e1e5e9;
    border-radius: 10px;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.search-box input:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.search-icon {
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    color: #999;
    font-size: 1.1rem;
}

.filter-buttons {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

.filter-btn {
    padding: 8px 16px;
    border: 2px solid #e1e5e9;
    border-radius: 25px;
    background: white;
    color: #666;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 0.9rem;
    font-weight: 500;
}

.filter-btn:hover {
    border-color: #667eea;
    color: #667eea;
    transform: translateY(-1px);
}

.filter-btn.active {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border-color: #667eea;
}

.empty-state {
    text-align: center;
    padding: 60px 20px;
    background: white;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.empty-icon {
    font-size: 4rem;
    margin-bottom: 20px;
    opacity: 0.5;
}

.empty-state h3 {
    color: #333;
    margin-bottom: 10px;
    font-size: 1.5rem;
}

.empty-state p {
    color: #666;
    margin-bottom: 30px;
    font-size: 1.1rem;
}

.cta-button {
    display: inline-block;
    padding: 12px 25px;
    background: linear-gradient(45deg, #ff6b6b, #ee5a24);
    color: white;
    text-decoration: none;
    border-radius: 25px;
    font-weight: 600;
    transition: all 0.3s ease;
}

.cta-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(255, 107, 107, 0.3);
}

.reviews-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 25px;
    margin-bottom: 30px;
}

.review-card {
    background: white;
    border-radius: 15px;
    padding: 25px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    border: 1px solid #f0f0f0;
}

.review-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.review-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.review-type {
    display: flex;
    align-items: center;
    gap: 8px;
}

.type-icon {
    font-size: 1.5rem;
}

.type-label {
    font-weight: 600;
    color: #333;
    font-size: 0.9rem;
    display: flex;
    align-items: center;
    gap: 5px;
}

.voice-badge {
    font-size: 0.8rem;
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    padding: 2px 6px;
    border-radius: 10px;
    font-weight: 500;
}

.review-actions {
    display: flex;
    gap: 8px;
}

.action-btn {
    padding: 6px 8px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 1rem;
}

.action-btn.copy {
    background: #e3f2fd;
    color: #1976d2;
}

.action-btn.copy:hover {
    background: #bbdefb;
    transform: scale(1.1);
}

.action-btn.delete {
    background: #ffebee;
    color: #d32f2f;
}

.action-btn.delete:hover {
    background: #ffcdd2;
    transform: scale(1.1);
}

.review-content {
    margin-bottom: 20px;
}

.review-title {
    font-size: 1.3rem;
    margin: 0 0 5px 0;
    color: #333;
    font-weight: 600;
}

.review-category {
    color: #666;
    margin: 0 0 15px 0;
    font-size: 0.9rem;
}

.review-rating {
    margin-bottom: 15px;
    font-size: 1.1rem;
}

.review-text {
    color: #555;
    line-height: 1.6;
    font-size: 0.95rem;
}

.review-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    border-top: 1px solid #f0f0f0;
}

.review-date {
    font-size: 0.85rem;
    color: #999;
}

.review-tone {
    font-size: 0.85rem;
    color: #667eea;
    font-weight: 500;
    text-transform: capitalize;
    background: rgba(102, 126, 234, 0.1);
    padding: 4px 8px;
    border-radius: 12px;
}

.history-stats {
    text-align: center;
    color: #666;
    font-size: 0.9rem;
    background: white;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
    .history-header h1 {
        font-size: 2rem;
    }
    
    .filters-section {
        padding: 20px;
    }
    
    .filter-buttons {
        gap: 8px;
    }
    
    .filter-btn {
        padding: 6px 12px;
        font-size: 0.8rem;
    }
    
    .reviews-grid {
        grid-template-columns: 1fr;
        gap: 20px;
    }
    
    .review-card {
        padding: 20px;
    }
    
    .review-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 15px;
    }
    
    .review-actions {
        align-self: flex-end;
    }
} 