.voice-review {
    min-height: 100vh;
    background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
    padding: 20px;
}

.voice-review-container {
    max-width: 1200px;
    margin: 0 auto;
    background: white;
    border-radius: 20px;
    padding: 40px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
}

.voice-review-header {
    text-align: center;
    margin-bottom: 40px;
}

.voice-review-header h1 {
    color: #333;
    margin-bottom: 10px;
    font-size: 2.5rem;
    font-weight: 700;
}

.voice-review-header p {
    color: #666;
    font-size: 1.1rem;
    max-width: 600px;
    margin: 0 auto;
}

.voice-review-content {
    display: grid;
    gap: 30px;
}

.settings-panel {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 25px;
    background: #f8f9fa;
    border-radius: 15px;
    border: 1px solid #e1e5e9;
}

.setting-group {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.setting-group label {
    font-weight: 600;
    color: #333;
    font-size: 0.9rem;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.setting-input {
    padding: 12px;
    border: 2px solid #e1e5e9;
    border-radius: 10px;
    font-size: 1rem;
    background: white;
    transition: all 0.3s ease;
}

.setting-input:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.rating-input {
    display: flex;
    align-items: center;
    gap: 8px;
}

.star-btn {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.3s ease;
    opacity: 0.3;
}

.star-btn.active {
    opacity: 1;
    transform: scale(1.1);
}

.star-btn:hover {
    transform: scale(1.1);
}

.rating-text {
    font-weight: 600;
    color: #333;
    margin-left: 10px;
}

.voice-input-section {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 25px;
    border: 1px solid #e1e5e9;
}

.voice-input-section h2 {
    color: #333;
    margin-bottom: 20px;
    font-size: 1.5rem;
    font-weight: 600;
}

.transcript-display {
    margin-top: 20px;
    padding: 20px;
    background: white;
    border-radius: 12px;
    border: 1px solid #e1e5e9;
}

.transcript-display h3 {
    color: #333;
    margin-bottom: 10px;
    font-size: 1.1rem;
    font-weight: 600;
}

.transcript-display p {
    color: #555;
    line-height: 1.6;
    margin-bottom: 15px;
    padding: 15px;
    background: #f8f9fa;
    border-radius: 8px;
    border-left: 4px solid #667eea;
}

.transcript-actions {
    display: flex;
    gap: 10px;
}

.analyze-btn,
.clear-btn {
    padding: 10px 20px;
    border: none;
    border-radius: 8px;
    font-size: 0.9rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.analyze-btn {
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
}

.analyze-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(102, 126, 234, 0.3);
}

.clear-btn {
    background: #dc3545;
    color: white;
}

.clear-btn:hover {
    background: #c82333;
    transform: translateY(-2px);
}

.analyzing-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    padding: 30px;
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    border-radius: 15px;
    font-size: 1.1rem;
    font-weight: 600;
}

.loading-spinner {
    width: 24px;
    height: 24px;
    border: 3px solid rgba(255, 255, 255, 0.3);
    border-top: 3px solid white;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.analysis-section {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 25px;
    border: 1px solid #e1e5e9;
}

.analysis-section h2 {
    color: #333;
    margin-bottom: 20px;
    font-size: 1.5rem;
    font-weight: 600;
}

.generated-review-section {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 25px;
    border: 1px solid #e1e5e9;
}

.generated-review-section h2 {
    color: #333;
    margin-bottom: 20px;
    font-size: 1.5rem;
    font-weight: 600;
}

.generated-review {
    background: white;
    border-radius: 12px;
    padding: 25px;
    border: 1px solid #e1e5e9;
}

.review-content {
    margin-bottom: 20px;
}

.review-content pre {
    white-space: pre-wrap;
    font-family: inherit;
    line-height: 1.6;
    color: #333;
    margin: 0;
    padding: 20px;
    background: #f8f9fa;
    border-radius: 8px;
    border-left: 4px solid #28a745;
}

.review-actions {
    display: flex;
    gap: 10px;
}

.copy-btn {
    padding: 10px 20px;
    background: #28a745;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 0.9rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.copy-btn:hover {
    background: #218838;
    transform: translateY(-2px);
}

.saved-reviews-section {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 25px;
    border: 1px solid #e1e5e9;
}

.saved-reviews-section h2 {
    color: #333;
    margin-bottom: 20px;
    font-size: 1.5rem;
    font-weight: 600;
}

.saved-reviews {
    display: grid;
    gap: 15px;
}

.saved-review-item {
    background: white;
    border-radius: 12px;
    padding: 20px;
    border: 1px solid #e1e5e9;
    transition: all 0.3s ease;
}

.saved-review-item:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.review-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    flex-wrap: wrap;
    gap: 10px;
}

.review-type {
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    padding: 4px 12px;
    border-radius: 15px;
    font-size: 0.8rem;
    font-weight: 600;
    text-transform: uppercase;
}

.review-rating {
    font-size: 0.9rem;
}

.review-date {
    color: #666;
    font-size: 0.8rem;
}

.review-text {
    color: #555;
    line-height: 1.6;
    margin: 0;
}

@media (max-width: 768px) {
    .voice-review-container {
        padding: 20px;
    }
    
    .voice-review-header h1 {
        font-size: 2rem;
    }
    
    .settings-panel {
        grid-template-columns: 1fr;
    }
    
    .transcript-actions,
    .review-actions {
        flex-direction: column;
    }
    
    .review-header {
        flex-direction: column;
        align-items: flex-start;
    }
} 