.voice-analysis-container {
    background: white;
    border-radius: 20px;
    padding: 25px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    margin: 20px 0;
    border: 1px solid #e1e5e9;
}

.analysis-error {
    text-align: center;
    padding: 40px 20px;
    color: #dc3545;
}

.analysis-error .error-icon {
    font-size: 3rem;
    margin-bottom: 15px;
    display: block;
}

.analysis-error h3 {
    margin-bottom: 10px;
    font-size: 1.5rem;
    font-weight: 600;
}

.analysis-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;
    padding-bottom: 15px;
    border-bottom: 2px solid #f8f9fa;
}

.provider-badge {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px 16px;
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 600;
}

.provider-icon {
    font-size: 1.1rem;
}

.analysis-actions {
    display: flex;
    gap: 10px;
}

.action-btn {
    padding: 8px 12px;
    background: #f8f9fa;
    border: 1px solid #e1e5e9;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.action-btn:hover {
    background: #e9ecef;
    transform: translateY(-1px);
}

.analysis-summary {
    margin-bottom: 25px;
}

.sentiment-section {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;
}

.sentiment-indicator {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 20px;
    border-radius: 25px;
    color: white;
    font-weight: 600;
    font-size: 1rem;
    min-width: 120px;
    justify-content: center;
}

.sentiment-emoji {
    font-size: 1.2rem;
}

.confidence-meter {
    flex: 1;
    display: flex;
    align-items: center;
    gap: 10px;
}

.confidence-label {
    font-size: 0.9rem;
    color: #666;
    font-weight: 500;
    min-width: 80px;
}

.confidence-bar {
    flex: 1;
    height: 8px;
    background: #e9ecef;
    border-radius: 4px;
    overflow: hidden;
}

.confidence-fill {
    height: 100%;
    border-radius: 4px;
    transition: width 0.5s ease;
}

.confidence-value {
    font-size: 0.9rem;
    font-weight: 600;
    color: #333;
    min-width: 40px;
    text-align: right;
}

.stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 15px;
}

.stat-item {
    text-align: center;
    padding: 15px;
    background: #f8f9fa;
    border-radius: 12px;
    border: 1px solid #e1e5e9;
}

.stat-label {
    font-size: 0.8rem;
    color: #666;
    font-weight: 500;
    margin-bottom: 5px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.stat-value {
    font-size: 1.2rem;
    font-weight: 700;
    color: #333;
}

.analysis-content {
    margin-bottom: 25px;
}

.content-section {
    margin-bottom: 25px;
}

.content-section h3 {
    font-size: 1.2rem;
    font-weight: 600;
    color: #333;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    gap: 8px;
}

.summary-text {
    line-height: 1.6;
    color: #555;
    font-size: 1rem;
    background: #f8f9fa;
    padding: 15px;
    border-radius: 10px;
    border-left: 4px solid #667eea;
}

.key-points-list {
    list-style: none;
    padding: 0;
}

.key-point-item {
    display: flex;
    align-items: flex-start;
    gap: 10px;
    padding: 8px 0;
    border-bottom: 1px solid #f0f0f0;
}

.key-point-item:last-child {
    border-bottom: none;
}

.point-bullet {
    color: #667eea;
    font-weight: bold;
    font-size: 1.2rem;
    line-height: 1.4;
}

.topics-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.topic-tag {
    padding: 6px 12px;
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    border-radius: 15px;
    font-size: 0.8rem;
    font-weight: 500;
}

.suggestions-list,
.action-items-list {
    list-style: none;
    padding: 0;
}

.suggestion-item,
.action-item {
    display: flex;
    align-items: flex-start;
    gap: 10px;
    padding: 10px 0;
    border-bottom: 1px solid #f0f0f0;
}

.suggestion-item:last-child,
.action-item:last-child {
    border-bottom: none;
}

.suggestion-icon,
.action-icon {
    font-size: 1.1rem;
    margin-top: 2px;
}

.transcript-display {
    background: #f8f9fa;
    padding: 15px;
    border-radius: 10px;
    border: 1px solid #e1e5e9;
}

.transcript-display p {
    margin: 0;
    line-height: 1.6;
    color: #555;
    font-style: italic;
}

.analysis-actions-footer {
    text-align: center;
    padding-top: 20px;
    border-top: 2px solid #f8f9fa;
}

.generate-review-btn {
    padding: 12px 24px;
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    border: none;
    border-radius: 25px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    display: inline-flex;
    align-items: center;
    gap: 8px;
}

.generate-review-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

.generate-review-btn:disabled {
    opacity: 0.7;
    cursor: not-allowed;
}

.loading-spinner {
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top: 2px solid white;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@media (max-width: 768px) {
    .voice-analysis-container {
        padding: 20px;
        margin: 15px 0;
    }
    
    .sentiment-section {
        flex-direction: column;
        align-items: stretch;
        gap: 15px;
    }
    
    .confidence-meter {
        flex-direction: column;
        align-items: stretch;
        gap: 8px;
    }
    
    .stats-grid {
        grid-template-columns: repeat(2, 1fr);
    }
    
    .analysis-header {
        flex-direction: column;
        gap: 15px;
        align-items: stretch;
    }
    
    .analysis-actions {
        justify-content: center;
    }
} 