.review-generator {
    min-height: 100vh;
    background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
    padding: 20px;
}

.review-container {
    max-width: 1200px;
    margin: 0 auto;
    background: white;
    border-radius: 20px;
    padding: 40px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
}

.review-header {
    text-align: center;
    margin-bottom: 40px;
}

.review-header h1 {
    color: #333;
    margin-bottom: 10px;
    font-size: 2.5rem;
    font-weight: 700;
}

.review-header p {
    color: #666;
    font-size: 1.1rem;
    max-width: 600px;
    margin: 0 auto;
}

.review-form {
    display: grid;
    gap: 30px;
}

.form-section {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 25px;
    border: 1px solid #e1e5e9;
}

.form-section h2 {
    color: #333;
    margin-bottom: 20px;
    font-size: 1.5rem;
    font-weight: 600;
}

.settings-grid {
    display: grid;
    gap: 25px;
}

.setting-group {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.setting-group label {
    font-weight: 600;
    color: #333;
    font-size: 1rem;
}

.type-options {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 10px;
}

.type-option {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px 10px;
    border: 2px solid #e1e5e9;
    border-radius: 12px;
    background: white;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 0.9rem;
    font-weight: 500;
}

.type-option:hover {
    border-color: #667eea;
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(102, 126, 234, 0.2);
}

.type-option.active {
    border-color: #667eea;
    background: linear-gradient(135deg, #667eea, #764ba2);
    color: white;
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

.type-icon {
    font-size: 1.5rem;
    margin-bottom: 5px;
}

.rating-input {
    display: flex;
    align-items: center;
    gap: 8px;
}

.star-btn {
    background: none;
    border: none;
    font-size: 1.8rem;
    cursor: pointer;
    transition: all 0.3s ease;
    opacity: 0.3;
}

.star-btn.active {
    opacity: 1;
    transform: scale(1.1);
}

.star-btn:hover {
    transform: scale(1.1);
}

.tone-options {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    gap: 10px;
}

.tone-option {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 15px;
    border: 2px solid #e1e5e9;
    border-radius: 10px;
    background: white;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 0.9rem;
    font-weight: 500;
}

.tone-option:hover {
    border-color: #667eea;
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(102, 126, 234, 0.2);
}

.tone-option.active {
    border-color: #667eea;
    background: linear-gradient(135deg, #667eea, #764ba2);
    color: white;
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

.tone-icon {
    font-size: 1.2rem;
}

.content-grid {
    display: grid;
    gap: 20px;
}

.content-group {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.content-group.full-width {
    grid-column: 1 / -1;
}

.content-group label {
    font-weight: 600;
    color: #333;
    font-size: 1rem;
}

.input-with-voice {
    display: grid;
    gap: 15px;
}

.input-with-voice textarea {
    padding: 15px;
    border: 2px solid #e1e5e9;
    border-radius: 10px;
    font-size: 1rem;
    font-family: inherit;
    resize: vertical;
    transition: all 0.3s ease;
    background: white;
}

.input-with-voice textarea:focus {
    outline: none;
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.form-actions {
    display: flex;
    gap: 15px;
    justify-content: center;
    margin-top: 20px;
}

.generate-btn {
    padding: 15px 30px;
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    border: none;
    border-radius: 25px;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 10px;
    min-width: 200px;
    justify-content: center;
}

.generate-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(102, 126, 234, 0.3);
}

.generate-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
}

.reset-btn {
    padding: 15px 25px;
    background: #f8f9fa;
    color: #666;
    border: 2px solid #e1e5e9;
    border-radius: 25px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.reset-btn:hover {
    background: #e9ecef;
    border-color: #adb5bd;
    transform: translateY(-2px);
}

.form-hint {
    text-align: center;
    margin-top: 20px;
    padding: 15px;
    background: linear-gradient(135deg, #e3f2fd, #f3e5f5);
    border-radius: 10px;
    border: 1px solid #e1e5e9;
}

.form-hint p {
    margin: 0;
    color: #333;
    font-size: 0.95rem;
    line-height: 1.5;
}

.form-hint strong {
    color: #667eea;
}

.loading-spinner {
    width: 20px;
    height: 20px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top: 2px solid white;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.result-section {
    display: grid;
    gap: 25px;
}

.result-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 15px;
}

.result-header h2 {
    color: #333;
    margin: 0;
    font-size: 1.8rem;
    font-weight: 600;
}

.result-actions {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

.copy-btn,
.analysis-toggle-btn {
    padding: 10px 20px;
    border: none;
    border-radius: 8px;
    font-size: 0.9rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.copy-btn {
    background: #28a745;
    color: white;
}

.copy-btn:hover {
    background: #218838;
    transform: translateY(-2px);
}

.analysis-toggle-btn {
    background: #667eea;
    color: white;
}

.analysis-toggle-btn:hover {
    background: #5a6fd8;
    transform: translateY(-2px);
}

.review-content {
    background: #f8f9fa;
    border: 1px solid #e1e5e9;
    border-radius: 12px;
    padding: 25px;
    max-height: 400px;
    overflow-y: auto;
}

.review-content pre {
    margin: 0;
    white-space: pre-wrap;
    font-family: inherit;
    line-height: 1.6;
    color: #333;
    font-size: 1rem;
}

.analyzing-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    padding: 20px;
    background: linear-gradient(45deg, #667eea, #764ba2);
    color: white;
    border-radius: 12px;
    font-size: 1rem;
    font-weight: 600;
}

.analysis-section {
    background: #f8f9fa;
    border-radius: 15px;
    padding: 25px;
    border: 1px solid #e1e5e9;
}

.analysis-section h3 {
    color: #333;
    margin-bottom: 20px;
    font-size: 1.3rem;
    font-weight: 600;
}

@media (max-width: 768px) {
    .review-container {
        padding: 20px;
    }
    
    .review-header h1 {
        font-size: 2rem;
    }
    
    .type-options {
        grid-template-columns: repeat(2, 1fr);
    }
    
    .tone-options {
        grid-template-columns: repeat(2, 1fr);
    }
    
    .form-actions {
        flex-direction: column;
    }
    
    .result-header {
        flex-direction: column;
        align-items: stretch;
    }
    
    .result-actions {
        justify-content: center;
    }
} 